import React from 'react';
import '../../App.css';
import 'font-awesome/css/font-awesome.min.css';
import Newsletter from './Newsletter/index';


export default class Footer extends React.Component {

    constructor(){
        super();
        this.state = {
            shops: [],
            informations: [],
            customerService:[] 
        }
    }

    componentDidMount() {
        fetch('http://localhost:3000/shops')
        .then(response => response.json())
        .then(shops => this.setState({shops}))
        .catch(err => console.log(err));

        fetch('http://localhost:3000/information')
        .then(response => response.json())
        .then(informations => this.setState({informations}))
        .catch(err => console.log(err));

        fetch('http://localhost:3000/customerService')
        .then(response => response.json())
        .then(customerService => this.setState({customerService}))
        .catch(err => console.log(err));
    }

    render() {
       const shops = this.state.shops.map(shop => <li key={shop.id}>{shop.menu}</li>)
       const informations = this.state.informations.map(information => <li key={information.id}>{information.menu}</li>)
       const customerService = this.state.customerService.map(customerServ => <li key={customerServ.id}>{customerServ.menu}</li>)

       let SocialIcon = [<i className="fa fa-facebook" aria-hidden="true"></i>
       , <i class="fa fa-twitter" aria-hidden="true"></i>
       , <i class="fa fa-pinterest-p" aria-hidden="true"></i>
       , <i class="fa fa-instagram" aria-hidden="true"></i>
   ]
   
       let SocialIconList = SocialIcon.map(function(SocialIcon, i){
           return <li key={i}>{SocialIcon}</li>
       })

        return (
            <div className="Footer">
                <div className="container">
                    <div className="row">
                        <div className="col span_2">
                            <div className="list">
                                <h3>shops</h3>
                                <ul>{shops}</ul>
                            </div>
                        </div>
                        <div className="col span_2">
                            <div className="list">
                                <h3>information</h3>
                                <ul>{informations}</ul>
                            </div>
                        </div>
                        <div className="col span_2">
                            <div className="list">
                                <h3>Customer Service</h3>
                                <ul>{customerService}</ul>
                            </div>
                        </div>
                        <div className="col span_6">
                            <div className="Connected">
                                <h3>stay connected</h3>
                                <ul>{SocialIconList}</ul>
                                <h3>subscribe <span>in</span> our newsletter</h3>
                                <Newsletter />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
