import React, { Component } from 'react';
import Slider from 'react-slick';
import '../../../App.css';
import '../../../slick.css';




class Home extends Component {
      
  render() {

    let banners = ["banner", "banner1", "banner2"];
    let images = banners.map ( (name, index) => {
        return (
            <div className="item">
                <img key={index} src={require(`../../../assets/${name}.jpg`)} alt="" />
                <div className="ImageContent">
                    <div className="InnerContent">
                        <h5>mid-season</h5>
                        <h3>sale</h3>
                        <div className="Upto">
                        <h5><span>up </span> to</h5>
                        </div>
                        <h4>50% off</h4>
                    </div>
                    <a href="javascript:void(0)">shop now</a>
                </div>
            </div>
        )
    });


     const settings = {
          dots: true,
          arrows: false,
          infinite: true,
          autoplay: true,
          speed: 500,
        }; 
      

    return (
        <div className="homeSlider">
            <Slider {...settings}>
                {images}
            </Slider>
        </div>
    )
    
  }
}

export default Home;
