import React from 'react';
import '../../../App.css';


class Newsletter extends React.Component {

    constructor() {
        super();
        this.state = {
            email: ""
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }


    async handleSubmit(){
        try {
            await fetch('http://localhost:3000/subscribers', {
                method: 'POST',
                headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    email: this.state.value
                })
            });
        } catch(errors) {

        }
    }

    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <div className="newsInput">
                    <input type="email" value={this.state.value} onChange={this.handleChange} placeholder="Your email address" className="email"></input>
                    <input type="submit" value="Subscribe" className="subbtn" onClick={this.handleSubmit.bind(this)}></input>
                </div>
            </form>
                            
        )
    }
    
}

export default Newsletter;
