import React from 'react';
import '../../../App.css'

const TopMenu = () => {
    let TopNav = [<li>welcome msg!</li>, <li>my account</li>, <li>my wishlist</li>, <li>Login</li>]
    let TopNavList = TopNav.map(function(TopNav, i){
        return <li key={i}>{TopNav}</li>
    })
    return (
        <div className="TopMenu">
            <ul>
            {TopNavList}
            </ul>
        </div>                                                                  
        );
}

export default TopMenu

